export default {
    name: "app-goods-preview-poster",

    props: {
        value: {
            type: Boolean,
            default() {
                return false;
            }
        },
        url: {
            type: String,
            default() {
                return '';
            }
        },

        disabled: {
            type: Boolean,
            default() {
                return false;
            }
        }
    },

    created: function () {
        this.showHidden = this.value;
    },
    watch: {
        value(newVal) {
            this.showHidden = newVal;
        },
        showHidden(newVal) {
            if (newVal) {
                this.getShareImg();
            }
        }
    },
    data() {
        return {
            showHidden: false,
            loading: true,
            shareImage: '',
        }
    },
    methods: {
        showHiddenClick() {
            this.$emit('close', false);
        },
        savePicture() {
            if (this.loading) return;
            this.$utils.batchSave(this.shareImage, 'image').then(() => {
                uni.showToast({title: '保存成功'});
            });
        },
        getShareImg() {
            if (!this.url) return;
            this.loading = true;
            this.$request({
                url: this.url,
            }).then(response => {
                if (response.code === 0) {
                    this.shareImage = response.data.pic_url;
                    this.loading = false;
                } else {
                    uni.showModal({
                        content: response.msg,
                        showCancel: false
                    });
                }
            });
        },
        preview() {
            uni.previewImage({
                urls: [this.shareImage]
            });
        }
    }
}
